// hooks/useInvoiceUpload.ts
"use client";

import { useState } from "react";
import {
  InvoiceRecordJson,
  PreviewRow,
  buildFileKey,
  isJsonFile,
  parseInvoiceFile,
} from "@/lib/types/invoices";
import { BACKEND_URL } from "@/lib/config";

export type ParsedFile = {
  id: string;
  file: File;
  rows: PreviewRow[];
  records: InvoiceRecordJson[];
};

type UseInvoiceUploadArgs = {
  onError: (message: string) => void;
};

export function useInvoiceUpload({ onError }: UseInvoiceUploadArgs) {
  const [files, setFiles] = useState<ParsedFile[]>([]);
  const [isParsing, setIsParsing] = useState(false);
  const [isUploading, setIsUploading] = useState(false);

  const hasFiles = files.length > 0;

  async function addFiles(fileList: File[]) {
    if (!fileList.length) return;

    setIsParsing(true);
    try {
      const existingKeys = new Set(files.map((f) => f.id));

      const uniqueFiles = fileList.filter((file) => {
        const key = buildFileKey(file);
        return !existingKeys.has(key) && isJsonFile(file);
      });

      const newParsed: ParsedFile[] = [];

      for (const file of uniqueFiles) {
        try {
          const { rows, records } = await parseInvoiceFile(file);
          newParsed.push({
            id: buildFileKey(file),
            file,
            rows,
            records,
          });
        } catch {
          onError("uploadDialog.invalidJson");
        }
      }

      if (newParsed.length) {
        setFiles((prev) => [...prev, ...newParsed]);
      }
    } finally {
      setIsParsing(false);
    }
  }

  function removeFile(id: string) {
    setFiles((prev) => prev.filter((f) => f.id !== id));
  }

  function reset() {
    setFiles([]);
    setIsParsing(false);
    setIsUploading(false);
  }

  async function uploadAll(): Promise<void> {
    if (!files.length) {
      onError("uploadDialog.invalidJson");
      return;
    }

    const allRecords: InvoiceRecordJson[] = files.flatMap((f) => f.records);

    setIsUploading(true);
    try {
      const res = await fetch(`${BACKEND_URL}/api/invoices/upload`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(allRecords),
      });

      if (!res.ok) {
        throw new Error("uploadDialog.errorUpload");
      }
    } catch {
      onError("uploadDialog.errorUpload");
      throw new Error("uploadDialog.errorUpload");
    } finally {
      setIsUploading(false);
    }
  }

  return {
    files,
    hasFiles,
    isParsing,
    isUploading,
    addFiles,
    removeFile,
    reset,
    uploadAll,
  };
}
