// components/invoices/UploadInvoicesDialog.tsx
"use client";

import { useState } from "react";
import { useI18n } from "@/i18n/i18nProvider";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogFooter,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { InvoicesDropzone } from "./InvoicesDropzone";
import { InvoicesFileList } from "./InvoicesFileList";
import { useInvoiceUpload } from "@/hooks/useInvoiceUpload";

type UploadInvoicesDialogProps = {
  onUploaded?: () => void;
};

export function UploadInvoicesDialog({
  onUploaded,
}: UploadInvoicesDialogProps) {
  const { t } = useI18n();
  const [open, setOpen] = useState(false);
  const [errorKey, setErrorKey] = useState<string | null>(null);
  const [isDragging, setIsDragging] = useState(false);

  const {
    files,
    hasFiles,
    isParsing,
    isUploading,
    addFiles,
    removeFile,
    reset,
    uploadAll,
  } = useInvoiceUpload({
    onError: (key) => setErrorKey(key),
  });

  const handleOpenChange = (next: boolean) => {
    setOpen(next);
    if (!next) {
      reset();
      setErrorKey(null);
      setIsDragging(false);
    }
  };

  const handleConfirm = async () => {
    try {
      await uploadAll();
      onUploaded?.();
      handleOpenChange(false);
    } catch {
      // errorKey už je nastavený v hooku
    }
  };

  return (
    <Dialog open={open} onOpenChange={handleOpenChange}>
      <DialogTrigger asChild>
        <Button
          className="rounded-full bg-[#00C3A0] hover:bg-[#009E85] text-white"
          size="sm"
        >
          {t("dashboard.uploadInvoices")}
        </Button>
      </DialogTrigger>

      <DialogContent className="sm:max-w-[900px] max-h-[80vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>{t("uploadDialog.title")}</DialogTitle>
          <DialogDescription>{t("uploadDialog.helperText")}</DialogDescription>
        </DialogHeader>

        <div className="space-y-4">
          <InvoicesDropzone
            isDragging={isDragging}
            setIsDragging={setIsDragging}
            hasFiles={hasFiles}
            onFilesSelected={addFiles}
          />

          <InvoicesFileList files={files} onRemove={removeFile} />

          {isParsing && (
            <Badge variant="outline">{t("uploadDialog.parsing")}</Badge>
          )}

          {errorKey && (
            <div className="text-sm text-red-600 bg-red-50 border border-red-200 rounded-md px-3 py-2">
              {t(errorKey)}
            </div>
          )}
        </div>

        <DialogFooter>
          <Button
            variant="outline"
            type="button"
            onClick={() => handleOpenChange(false)}
          >
            {t("uploadDialog.cancel")}
          </Button>
          <Button
            type="button"
            onClick={handleConfirm}
            disabled={isUploading || !hasFiles}
            className="bg-[#00C3A0] hover:bg-[#009E85] text-white"
          >
            {isUploading
              ? t("uploadDialog.uploading")
              : t("uploadDialog.confirm")}
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
