// components/dashboard/Pagination.tsx
"use client";

import { Button } from "@/components/ui/button";
import { useI18n } from "@/i18n/i18nProvider";
import { PaginationProps } from "./dashboard.types";

export function Pagination({
  currentPage,
  totalPages,
  totalItems,
  isLoading,
  onPageChange,
}: PaginationProps) {
  const { t } = useI18n();

  if (totalItems === 0) return null;

  return (
    <div className="mt-6 flex items-center justify-between text-sm text-slate-600">
      <div>
        {/* i18n safe UI */}
        {t("dashboard.pagination.page")} {currentPage}{" "}
        {t("dashboard.pagination.of")} {totalPages} ·{" "}
        {t("dashboard.pagination.total")} {totalItems}{" "}
        {t("dashboard.pagination.shipments")}
      </div>

      <div className="flex gap-2">
        <Button
          type="button"
          variant="outline"
          size="sm"
          disabled={currentPage <= 1 || isLoading}
          onClick={() => onPageChange(currentPage - 1)}
        >
          {t("dashboard.prevPage")}
        </Button>

        <Button
          type="button"
          variant="outline"
          size="sm"
          disabled={currentPage >= totalPages || isLoading}
          onClick={() => onPageChange(currentPage + 1)}
        >
          {t("dashboard.nextPage")}
        </Button>
      </div>
    </div>
  );
}
