"use client";

import { Button } from "@/components/ui/button";
import { useI18n } from "@/i18n/i18nProvider";
import { SUPPORTED_LANGUAGES, LanguageCode } from "@/i18n/languages";

export function LanguageSwitcher() {
  const { locale, setLocale, t } = useI18n();

  return (
    <div className="inline-flex items-center rounded-full bg-white border border-slate-200 px-1 py-1 text-xs shadow-sm">
      {SUPPORTED_LANGUAGES.map(({ code, labelKey }) => (
        <Button
          key={code}
          type="button"
          size="sm"
          variant={locale === code ? "default" : "ghost"}
          className="rounded-full px-3"
          aria-label={t(labelKey)}
          onClick={() => setLocale(code as LanguageCode)}
        >
          {t(labelKey)}
        </Button>
      ))}
    </div>
  );
}
