// components/dashboard/FiltersBar.tsx
"use client";

import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { useI18n } from "@/i18n/i18nProvider";
import { FiltersBarProps } from "./dashboard.types";

export function FiltersBar({
  companyFilter,
  onCompanyFilterChange,
  selectedDate,
  onSelectedDateChange,
  sortOrder,
  onSortOrderChange,
  onClearFilters,
}: FiltersBarProps) {
  const { t } = useI18n();

  return (
    <div className="flex flex-wrap items-center justify-between gap-3 mb-4">
      {/* ---------- Company Filter ---------- */}
      <Input
        value={companyFilter}
        onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
          onCompanyFilterChange(e.target.value)
        }
        placeholder={t("dashboard.filterPlaceholder")}
        className="w-60 rounded-full bg-white border border-slate-200 shadow-sm"
      />

      <div className="flex items-center gap-3">
        {/* ---------- Date Filter ---------- */}
        <div className="flex items-center gap-2">
          <span className="text-xs text-slate-500">
            {t("dashboard.dateLabel")}
          </span>
          <Input
            type="date"
            value={selectedDate}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
              onSelectedDateChange(e.target.value)
            }
            className="rounded-full bg-white border border-slate-200 shadow-sm text-sm px-3 py-1 h-8"
          />
        </div>

        {/* ---------- Sort Dropdown ---------- */}
        <div className="flex items-center gap-2">
          <span className="text-xs text-slate-500">
            {t("dashboard.sortLabel")}
          </span>
          <select
            value={sortOrder}
            onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
              onSortOrderChange(e.target.value as "newest" | "oldest")
            }
            className="rounded-full border border-slate-300 bg-white px-3 py-1 text-sm h-8"
          >
            <option value="newest">{t("dashboard.sortNewest")}</option>
            <option value="oldest">{t("dashboard.sortOldest")}</option>
          </select>
        </div>

        {/* ---------- Clear Filters Button ---------- */}
        <Button
          type="button"
          variant="ghost"
          size="sm"
          className="rounded-full px-3"
          onClick={onClearFilters}
        >
          {t("dashboard.clearFilters")}
        </Button>
      </div>
    </div>
  );
}
