// components/invoices/InvoicesDropzone.tsx
"use client";

import { useRef } from "react";
import { useI18n } from "@/i18n/i18nProvider";
import { cn } from "@/lib/utils";

type InvoicesDropzoneProps = {
  isDragging: boolean;
  setIsDragging: (value: boolean) => void;
  hasFiles: boolean;
  onFilesSelected: (files: File[]) => void;
};

export function InvoicesDropzone({
  isDragging,
  setIsDragging,
  hasFiles,
  onFilesSelected,
}: InvoicesDropzoneProps) {
  const { t } = useI18n();
  const fileInputRef = useRef<HTMLInputElement | null>(null);

  const handleFileInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const fileList = e.target.files;
    if (!fileList || fileList.length === 0) return;
    onFilesSelected(Array.from(fileList));
    e.target.value = "";
  };

  const handleDragOver = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.dataTransfer.dropEffect = "copy";
    setIsDragging(true);
  };

  const handleDragLeave = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    setIsDragging(false);
  };

  const handleDrop = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    setIsDragging(false);
    const files = Array.from(e.dataTransfer.files);
    onFilesSelected(files);
  };

  const openFileDialog = () => fileInputRef.current?.click();

  return (
    <div
      onDragOver={handleDragOver}
      onDragLeave={handleDragLeave}
      onDrop={handleDrop}
      className={cn(
        "border border-dashed rounded-2xl bg-slate-50/50 transition px-4",
        hasFiles ? "py-4" : "py-8",
        isDragging ? "border-[#00C3A0] bg-emerald-50/50" : "border-slate-200"
      )}
    >
      <input
        ref={fileInputRef}
        type="file"
        accept="application/json"
        multiple
        className="hidden"
        onChange={handleFileInputChange}
      />

      {!hasFiles ? (
        <div
          role="button"
          tabIndex={0}
          onClick={openFileDialog}
          onKeyDown={(e) => {
            if (e.key === "Enter" || e.key === " ") {
              e.preventDefault();
              openFileDialog();
            }
          }}
          className="w-full flex flex-col items-center gap-1 text-center cursor-pointer"
        >
          <span className="text-slate-600">
            {t("uploadDialog.dropHereText")}
          </span>
          <span className="text-xs text-slate-400">
            {t("uploadDialog.dropHereSubtext")}
          </span>
        </div>
      ) : (
        <button
          type="button"
          onClick={openFileDialog}
          className="text-xs text-[#00997F] underline hover:text-[#007a64]"
        >
          {t("uploadDialog.addMore")}
        </button>
      )}
    </div>
  );
}
