// components/invoices/InvoicesFileList.tsx
"use client";

import { ParsedFile } from "@/hooks/useInvoiceUpload";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { ChevronRight, Trash2 } from "lucide-react";
import { cn } from "@/lib/utils";
import { useState } from "react";
import { useI18n } from "@/i18n/i18nProvider";

type InvoicesFileListProps = {
  files: ParsedFile[];
  onRemove: (id: string) => void;
};

export function InvoicesFileList({ files, onRemove }: InvoicesFileListProps) {
  const { t } = useI18n();
  const [expandedId, setExpandedId] = useState<string | null>(null);

  if (!files.length) return null;

  return (
    <div className="mt-3 flex flex-col gap-3">
      {files.map((pf) => {
        const isExpanded = expandedId === pf.id;

        return (
          <div
            key={pf.id}
            className="border border-slate-200 rounded-xl bg-white"
          >
            <div className="flex items-center justify-between px-4 py-2">
              <button
                type="button"
                onClick={() => setExpandedId(isExpanded ? null : pf.id)}
                className="flex items-center gap-2 text-left"
              >
                <ChevronRight
                  className={cn(
                    "h-4 w-4 transition-transform",
                    isExpanded && "rotate-90"
                  )}
                />
                <div className="flex flex-col">
                  <span className="text-sm font-medium truncate max-w-xs sm:max-w-sm">
                    {pf.file.name}
                  </span>
                  <span className="text-xs text-slate-400">
                    {pf.rows.length} {t("uploadDialog.rowsLabel")}
                  </span>
                </div>
              </button>

              <button
                type="button"
                onClick={() => onRemove(pf.id)}
                className="inline-flex items-center justify-center rounded-full p-1.5 text-slate-400 hover:text-red-600 hover:bg-red-50 transition"
              >
                <Trash2 className="w-4 h-4" />
              </button>
            </div>

            {isExpanded && pf.rows.length > 0 && (
              <div className="border-t border-slate-100 max-h-56 overflow-y-auto">
                <Table className="w-full text-sm table-fixed">
                  <TableHeader>
                    <TableRow>
                      <TableHead className="w-[140px]">
                        {t("uploadDialog.colTracking")}
                      </TableHead>
                      <TableHead className="w-[220px]">
                        {t("uploadDialog.colCompany")}
                      </TableHead>
                      <TableHead className="w-[100px]">
                        {t("uploadDialog.colProvider")}
                      </TableHead>
                      <TableHead className="w-[120px]">
                        {t("uploadDialog.colRoute")}
                      </TableHead>
                      <TableHead className="text-right w-[90px]">
                        {t("uploadDialog.colPrice")}
                      </TableHead>
                      <TableHead className="text-right w-[90px]">
                        {t("uploadDialog.colWeight")}
                      </TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {pf.rows.map((row) => (
                      <TableRow key={row.id}>
                        <TableCell>{row.trackingNumber}</TableCell>
                        <TableCell className="truncate">
                          {row.companyName}
                        </TableCell>
                        <TableCell>{row.provider}</TableCell>
                        <TableCell>
                          {row.originCountry} → {row.destinationCountry}
                        </TableCell>
                        <TableCell className="text-right text-[#00997F] font-semibold">
                          {row.invoicedPrice.toFixed(2)}
                        </TableCell>
                        <TableCell className="text-right">
                          {row.invoicedWeight ?? "-"}
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </div>
            )}
          </div>
        );
      })}
    </div>
  );
}
